<template>
    <div id='index-login' v-loading.fullscreen.lock='showloading'>
        <el-form :label-position="labelPosition" label-width="80px" :model="loginForm" id='index-login-form'>
            <el-form-item label="E-Mail">
                <el-input v-model="loginForm.email"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input type="password" v-model="loginForm.password"></el-input>
            </el-form-item>
            <el-button type="primary" @click='loginPost' :loading="clickLoading" style="width: 100%"></el-button>
        </el-form>
        <router-link to='register' tag='div' class="t-btn"><strong> </strong><i style="width: 25px" class="el-icon-arrow-right"></i></router-link>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                labelPosition: 'top',
                clickLoading: false,

                loginForm: {
                    email: '',
                    password: ''
                },
                //
                switchLogin: true,
                //
                showloading: false,
            }
        },
        methods: {
            //
            loginPost: function (mes) {
                this.clickLoginBtn = true

                this.$http.post('http://acrossw.cn/user/login', this.loginForm).then(r => {
                    if (r.body.type == 'success') {
                        this.clickLoginBtn = false
                        this.showloading = true;
                    } else if (r.body.type == 'fail') {
                        let errorMes = ''
                        r.body.mes.forEach((v, i) => {
                            errorMes += v + ' / '
                        })
                        this.$notify.error({
                            message: errorMes
                        });
                    }
                }, r => {

                });

            },
        }
    }

</script>


